import React, { Component } from 'react';
import WaifuCard from '../../components/WaifuCard/WaifuCard'
import { Fade, Col, Row, Container } from 'reactstrap';

import './AuctionsHistory.css'

import * as Api from '../../dist/api'

const WaifusList = ({waifusid}) => (
    <>
    {waifusid.map(idW => (
        <Col lg="3" md="4" sm="6" key={idW} >
            <WaifuCard id={idW} mainButtonText="See on the wiki"/>
        </Col>
    ))}
    </>
); 

class AuctionsHistory extends Component {

    constructor(props) {
        super(props);

        this.toggle = this.toggle.bind(this);
        this.toggleFade = this.toggleFade.bind(this);
        this.state = {
            collapse: true,
            fadeIn: true,
            timeout: 300,
            idswaifus: []
        };
        Api.getWaifus((waifus)=>{
            this.setState({
                idswaifus: [...Array(waifus[0]).keys()],
            });
        })
    }

    toggle() {
        this.setState({ collapse: !this.state.collapse });
    }

    toggleFade() {
        this.setState((prevState) => { return { fadeIn: !prevState }});
    }

    render() {
        const waifu={
            "name": "Kashiwazaki Sena",
            "image": "https://api.waifuchain.moe/image/8.jpeg",
            "description": "The wealthy blonde daughter of the school's headteacher, Sena shows a dislike of all things \"ordinary.\" She is both attractive and academically successful, but her resulting arrogance leads her to be disliked by her female peers. Their admiration eludes her, much as she desires it; she treats her male classmates only as servants to be used, although many of them would like to be her boyfriend. Nonetheless, she holds Kodaka to a higher standard. Kodaka concludes that she is the weirdest member of the club, as she is able to warrant weird and disgusted looks not only from Yozora and Kobato, but also from Yukimura and Rika.\r\n\r\nShe enjoys playing video games—including dating sims and eroge—partially because they allow her to make female (if virtual) friends. Yozora calls her \"Meat\" (Niku) an insulting nickname which she secretly likes because it is the first she has ever received. Her father is a friend of Kodaka's father, and pulled strings so that Kodaka could attend St. Chronica's.\r\n\r\nShe really likes Kobato, since she always get really excited when she sees her. Usually, when Sena and Yozora fight, Sena usually runs from the room crying and shouting \"Stupid Yozora! Go die!\".\r\n\r\nIt is hinted that she has feelings for Kodaka."
          }
          const waifu2={
            "name": "Hilda",
            "image": "https://api.waifuchain.moe/image/88.jpeg",
            "description": "Hilda (Japanese: トウコ Tōko, romanized as Touko) is the female player character in Pokémon Black and White."
          }
        return (
            <div className="animated fadeIn">
            <div id="waifu-countdown"></div>
            <Fade timeout={this.state.timeout} in={this.state.fadeIn}>
                <Container>
                    <h1>Auction history</h1>
                    <Row id="waifusOnAuciton" className="main-table">
                        <WaifusList waifusid={this.state.idswaifus}/>
                    </Row>
                </Container>
            </Fade>
            </div>
        )
    }
}

export default AuctionsHistory;

